export default {
	data() {
		return {
			id:'',
			windowHeight: '',
			coupon:[],
		}
	},
	onLoad() {
		var that = this;
		that.windowHeight = this.statusBarHeight + 44;
		uni.getStorage({
			key: 'userinfo',
			success: function(res) {
				that.id = res.data.id;
				that.getinfo();
			}
		})
	},
	methods: {
		getinfo:function(){
			var that=this;
			uni.request({
				url: this.serveipd + "/api/merchant/money/groupTickets",
				method: 'GET',
				data: {
					merchantId:that.id,
				},
				success: res => {
					console.log(res.data);
					that.coupon = res.data.data.datas;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		preserve:function() {
			uni.redirectTo({
				url: '/pages/tool/couponrecord',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
	}
}
